@import url(https://fonts.googleapis.com/css?family=Indie+Flower&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:sans-serif;touch-action:manipulation}.App{font-size:16px;height:100vh;width:100vw;overflow:hidden;display:flex;flex-direction:column;justify-content:space-between;align-items:center;background:#8360c3;background:linear-gradient(to bottom, #2193b0, #8360c3)}

.settings__panel{background-color:rgba(255,255,255,0.9);width:100%;border-radius:10px 10px 0 0;height:50%;box-shadow:0 14px 28px rgba(0,0,0,0.25),0 10px 10px rgba(0,0,0,0.22);transition:all 0.2s;display:flex;justify-content:space-between;flex-direction:column;align-items:center;overflow:hidden}.settings__panel--closed{height:50px}.settings__panel--closed .categories,.settings__panel--closed .bottom{display:none}.settings__toggle{color:#555;width:100%;text-align:center;height:50px}.categories{width:90%;display:flex;align-items:center;justify-content:center;flex-wrap:wrap;overflow:auto}.categories .category{margin:2px;border-radius:5px;font-size:14px;padding:2px 5px;border:solid 1px;cursor:pointer;text-transform:capitalize}.categories .category--selected{background-color:#2193b0;color:white}.bottom{display:flex;width:100%;flex-direction:column;align-items:center;justify-content:center}.languages button{background:none;color:#2193b0;border:none;font-weight:bold}.credits{color:#555;padding:5px 15px;width:90%;align-items:center;justify-content:center;display:flex;font-size:0.8em}.credits a{text-decoration:none;color:#8360c3}

.suggestion{flex-grow:1;height:90%;width:90%;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly}.suggestion__display{flex-grow:1;max-height:50%;display:flex;flex-direction:column;justify-content:space-evenly;padding:10px;align-items:center;text-align:center;text-transform:capitalize;font-size:1em}.suggestion__display .onboarding{text-transform:none;color:white;font-size:2em;font-family:"Indie Flower", cursive}.suggestion__display .suggestion__title{margin:5% 0;color:white;font-size:2.5em;font-family:"Indie Flower", cursive}.button{background:transparent;color:#fff;border-radius:50px;border:none;padding:0.8rem 2rem;font:16px "Margarine", sans-serif;outline:none;cursor:pointer;position:relative;transition:0.2s ease-in-out;letter-spacing:2px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.button--primary{min-width:200px;background:#2193b0}

